import React, { Component } from 'react';
import Talent from './Talent';

export default class Tier extends Component {
    render() {
        var listItems = this.props.tier.map((talent, index) =>
            <Talent key={talent.toString()} talent={talent} key={index.toString()} onClick=""></Talent>
        )
        return (
            <div className="Tier">
                {listItems}
            </div>
        );
    }
}




