import React, { Component } from 'react';
import './Tree.css';
import ModelData from '../Model'
import Description from './Description'
import ReactDOM from 'react-dom';

export default class Talent extends Component {
    toggleDescription() {
        Description.show("//wow.zamimg.com/images/wow/icons/large/" + this.props.talent.spell.icon + ".jpg", this.props.talent.spell.name, this.props.talent.spell.castTime, this.props.talent.spell.description);
    }
    render() {
        var src = "//wow.zamimg.com/images/wow/icons/large/" + this.props.talent.spell.icon + ".jpg"
        var href = "http://www.wowdb.com/spells/" + this.props.talent.spell.id;
        var text = this.props.talent.spell.name;
        var tier = this.props.talent.tier;
        var column = this.props.talent.column + 1;

        var activeClass = ModelData.TALENTS[tier] == column ? "Talent-active" : "";
        var classTalent = "Talent " + activeClass;
        return (
            <span className={classTalent} onClick={() => { ModelData.TALENTS[tier] = column }}>
                <div className="talent-inner">
                    <img src={src} onClick={this.toggleDescription.bind(this)} />
                    <span >{text}</span>
                </div>
            </span>
        );
    }
}


