import React, { Component } from 'react';
var instance;
export default class Description extends Component {
    componentDidMount() {
        instance = this;
        instance.values = {
            icon: "icon",
            name: "name",
            type: "type",
            text: "text",
            show: false
        };
    }
    static show(icon, name, type, text) {
        var isShow = true;
        if (name == instance.values.name && instance.values.show) isShow = false;
        instance.values = {
            icon: icon,
            name: name,
            type: type,
            text: text,
            show: isShow
        };
    }
    hide() {
        instance.values.show = false;
    }
    render() {
        if (!instance || !instance.values) return null;
        if (!instance.values.show) return null;
        return (
            <div className="Description" onClick={this.hide}>
                <div className="Description-header">
                    <img src={this.values.icon} />
                    <div>{this.values.name}</div>
                </div>
                <div>{this.values.type}</div>
                <div>{this.values.text}</div>
            </div >
        );
    }
}




