import React, { Component } from 'react';
import Tree from './tree/Tree'
import CharSelector from './navigation/CharSelector'
import './App.css';
import talentsJSON from "./assets/json/talents.json";
import ModelData from './Model'
import Description from './tree/Description'

class App extends Component {
  constructor(props) {
    super(props);
    this.state = { role: talentsJSON[ModelData.CHARACTER].class };
    setInterval(() => {
      this.setState({ role: talentsJSON[ModelData.CHARACTER].class });
    }, 100);
  }
  render() {
    if (talentsJSON == undefined) return (<div></div>);
    if (talentsJSON[1].class == undefined) return (<div></div>);
    var spec = talentsJSON[ModelData.CHARACTER].specs[ModelData.SPEC].name;
    return (
      <div className="App">
        <CharSelector chars={talentsJSON} />
        <Tree talents={talentsJSON[ModelData.CHARACTER]} spec={spec} />
        < Description />
      </div>
    );
  }
}

export default App;



