import React, { Component } from 'react';
import './Navigation.css';
import ModelData from '../Model'
export default class CharSelector extends Component {
    constructor(props) {
        super(props);
    }
    setSpecClass(index) {
        return ModelData.SPEC == index ? "Char-active" : "";
    }
    setCharClass(index) {
        return ModelData.CHARACTER == index ? "Char-active" : "";
    }

    selectClass(index) {
        ModelData.CHARACTER = index + 1;
        ModelData.TALENTS = [0, 0, 0, 0, 0, 0, 0, 0];
        console.log("Class = " + ModelData.CHARACTER);
    };
    render() {
        var charsArray = Object.values(this.props.chars);
        var chars = Array.from(charsArray).map((char, index) =>
            <span className={'Char ' + this.setCharClass(index + 1)} onClick={() => this.selectClass(index)} key={index.toString()}>
                <img src={"//wow.zamimg.com/images/wow/icons/medium/class_" + char.class.replace("-", "") + ".jpg"} />
            </span >
        )
        var specs = charsArray[ModelData.CHARACTER - 1].specs.map((spec, index) =>
            <span className={'Char ' + this.setSpecClass(spec.order)} onClick={() => ModelData.SPEC = spec.order} key={index.toString()}>
                <img src={"//wow.zamimg.com/images/wow/icons/medium/" + spec.icon.replace("-", "") + ".jpg"} />
            </span >
        )
        return (
            <div>
                <div className="CharSelector">
                    {chars}
                </div>
                <div className="SpecSelector">
                    {specs}
                </div>
            </div>
        );
    }
}

