import React, { Component } from 'react';
import Tier from './Tier';

export default class Tree extends Component {
  render() {
    var filteredTiers = [];
    this.props.talents.talents.forEach(tier => {
      var filteredTier = [];
      tier.forEach(talents => {
        var neededTalent;
        var indextT = talents.findIndex(t => t.spec != undefined && t.spec.name == this.props.spec);
        if (indextT >= 0)
          neededTalent = talents[indextT];
        else {
          var indextTN = talents.findIndex(t => t.spec == undefined);
          neededTalent = talents[indextTN];
        }
        filteredTier.push(neededTalent);
      });
      filteredTiers.push(filteredTier);
    });
    var listItems = filteredTiers.map((tier, index) =>
      <Tier tier={tier} key={index.toString()}></Tier>
    )
    return (
      <div>
        {listItems}
      </div>
    );
  }
}


